import React from 'react';

const Banner = ({ appName }) => {
  return (
    <div className="banner">
      <div className="container">
        <h1 className="logo-font">
          {appName.toLowerCase()}
        </h1>
        <p>A react-redux front end blogging platform.</p>
      </div>
    </div>
  );
};

export default Banner;


